@import '../../styles/variables';

.cookieButton{
    background-color: $color-dark-gray;
    color: $color-white;
    @include flex-center-row;
    font-size: 16px;
    font-weight: 600;
    font-family: $font-title;
    border-radius: 16px;
    padding: 15px 24px;
    transition: $transition-style;

    &:hover{
        background-color: $color-hover-gray;
    }
}